import React, { HTMLAttributes } from "react";
import { connect } from "react-redux";
import { withRouter, RouteComponentProps } from "react-router";
import styled from "../../styles/styled";
import { State } from "../../reducers";
import { Times, selectTimes, selectCanSeek } from "../../reducers/player";
import { seek } from "../../actions/player";
import { IconType, RoundButton } from "../core";
import TrackInfos from "./TrackInfos";
import Audio from "./Audio";
import Controls from "./Controls";
import Playback from "./Playback";
import Volume from "./Volume";

const Wrapper = styled.div`
  align-items: center;
  background: ${props => props.theme.background.light};
  box-sizing: border-box;
  box-shadow: 0 -2px 4px 2px rgba(0, 0, 0, 0.2);
  display: flex;
  height: ${props => props.theme.thickness.extraLarge}px;
  padding: ${props => props.theme.thickness.small}px;
  z-index: 2;
`;

const ThirdWrapper = styled.div`
  box-sizing: border-box;
  height: 100%;
`;

const LeftWrapper = styled(ThirdWrapper)`
  display: none;

  @media (min-width: ${({ theme }) => theme.breakpoints.extraSmall}px) {
    display: block;
    min-width: 25%;
  }
`;

const CenterWrapper = styled(ThirdWrapper)`
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;

  @media (min-width: ${({ theme }) => theme.breakpoints.extraSmall}px) {
    padding: 0 ${props => props.theme.thickness.small}px;
    max-width: 50%;
  }
`;

const RightWrapper = styled(ThirdWrapper)`
  align-items: center;
  display: none;
  justify-content: flex-end;

  @media (min-width: ${({ theme }) => theme.breakpoints.extraSmall}px) {
    display: flex;
    min-width: 25%;
  }
`;

const QueueButton = styled(RoundButton).attrs(() => ({
  iconType: IconType.PlaylistPlay
}))`
  flex-shrink: 0;
  margin: ${props => props.theme.thickness.extraSmall}px;
`;

interface Props extends RouteComponentProps {
  times: Times;
  canSeek: boolean;
  seek: (time: number) => void;
}

function Player({
  className,
  history,
  times,
  canSeek,
  seek
}: Props & HTMLAttributes<HTMLElement>) {
  function handleQueueClick() {
    history.push(`${process.env.PUBLIC_URL}/queue`);
  }

  return (
    <Wrapper className={className}>
      <LeftWrapper>
        <TrackInfos />
      </LeftWrapper>

      <CenterWrapper>
        <Controls />

        <Playback
          duration={times.duration}
          currentTime={times.currentTime}
          canSeek={canSeek}
          onSeek={seek}
        />
      </CenterWrapper>

      <RightWrapper>
        <QueueButton onClick={handleQueueClick} />
        <Volume />
      </RightWrapper>

      <Audio />
    </Wrapper>
  );
}

const mapState = (state: State) => ({
  times: selectTimes(state),
  canSeek: selectCanSeek(state)
});

const mapDispatch = {
  seek
};

export default withRouter(
  connect(
    mapState,
    mapDispatch
  )(Player)
);
